







import Vue from "vue";

import { pasticcio } from "@/ts/dep";

const ciccio: {
  text: string;
  age: number;
} = {
  text: "ciccio",
  age: 30
};

// using vue.extend enables type inference
export default Vue.extend({
  name: "About",
  props: {},
  data() {
    return {};
  },
  computed: {
    name(): string {
      return ciccio.text + pasticcio.text;
    }
  }
});
